// Roboto
@font-face {
  font-family: 'Roboto';
  src: url('static:fonts/roboto/roboto-regular-webfont.eot');
  src: url('static:fonts/roboto/roboto-regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('static:fonts/roboto/roboto-regular-webfont.woff2') format('woff2'),
    url('static:fonts/roboto/roboto-regular-webfont.woff') format('woff'),
    url('static:fonts/roboto/roboto-regular-webfont.ttf') format('truetype'),
    url('static:fonts/roboto/roboto-regular-webfont.svg#Roboto') format('svg');
